import { Button } from 'antd';
import axios from 'axios';
import { Navigate, NavLink, Outlet, useNavigate } from 'react-router-dom';
import './Home.css';
const Home: React.FC = () => {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
      {/* Outlet会被子元素替代 */}
      <Outlet />

      <div style={{ display: 'flex' }}>
        {/* 加end 点中会加一个类名active*/}
        <Button
          size="large"
          style={{
            flex: 0.5,
            height: '100px',
            borderRadius: '5px',
            border: '1px solid black',
            backgroundImage: `url(${require('./canPic.png')})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            fontWeight: '900',
            fontFamily: '-moz-initial',
          }}
          type="primary"
        >
          <NavLink className="Home-link" end to="/home/create">
            创建
          </NavLink>
        </Button>
        <Button
          size="large"
          style={{
            flex: 0.5,
            height: '100px',
            borderRadius: '5px',
            border: '1px solid black',
            backgroundImage: `url(${require('./dogPic.png')})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            fontWeight: '900',
            fontFamily: '-moz-initial',
          }}
          type="primary"
        >
          <NavLink className="Home-link" end to="/home/me">
            我
          </NavLink>
        </Button>
      </div>
    </div>
  );
};

export default Home;
