import { Button } from 'antd';
import { Link } from 'react-router-dom';

export default function SelectCreation() {
  return (
    <div style={{ flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'stretch' }}>
      <Button
        size="large"
        style={{
          backgroundColor: '#FCD208',
          flex: 0.5,
          backgroundImage: `url(${require('./singleVote.png')})`,
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover',
        }}
        shape="round"
      >
        <Link to="/create?type=single">
          <h2 style={{ color: 'white' }}>创建单选</h2>
        </Link>
      </Button>

      <Button
        size="large"
        style={{
          backgroundColor: '#FCD208',
          flex: 0.5,
          backgroundImage: `url(${require('./multipleVote.png')})`,
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover',
        }}
        shape="round"
      >
        <Link to="/create?type=multiple">
          <h2 style={{ color: 'white' }}>创建多选</h2>
        </Link>
      </Button>
    </div>
  );
}
