import { Button } from 'antd';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useForceLogin } from './hooks';
import Login from './Login';
import Register from './Register';

export default function Me() {
  useForceLogin();
  const [votes, setVotes] = useState<any>([]);

  const navigate = useNavigate();
  async function logout() {
    await axios.get('/account/logout');
    navigate('/login');
  }

  useEffect(() => {
    axios.get('/vote').then((res) => {
      setVotes(res.data.result);
    });
  }, []);
  async function deleteVote(id: any) {
    await axios.delete('/vote/' + id);
    setVotes(votes.filter((it: any) => it.voteId !== id));
  }
  return (
    <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
      <div style={{ textAlign: 'center' }}>
        <Button type="primary" style={{ width: '100px', height: '50px' }} size="large" onClick={logout}>
          <h3>登出</h3>
        </Button>
      </div>

      <div style={{ overflow: 'auto', height: '550px' }}>
        {votes.map((vote: any, idx: number) => {
          return (
            <div>
              <h3
                style={{
                  margin: 0,
                  color: 'orange',
                  fontWeight: 700,
                  border: '1px solid black',
                }}
              >
                {vote.title}
              </h3>
              <div style={{ textAlign: 'center' }}>
                <Link to={`/vote/${vote.voteId}`}>
                  <span style={{}}>详情</span>
                </Link>
                <Button
                  onClick={() => {
                    deleteVote(vote.voteId);
                  }}
                  type="dashed"
                  style={{ marginLeft: '20px' }}
                >
                  <h3>删除</h3>
                </Button>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
