import { Button, Input } from 'antd';
import axios from 'axios';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useInput } from './hooks';

export default function Register() {
  var name = useInput('');
  var password = useInput('');
  var email = useInput('');
  var [avatarUrl, setAvatarUrl] = useState('');

  var navigate = useNavigate();

  async function handleFileSelect(e: React.ChangeEvent<HTMLInputElement>) {
    if (e.target.files && e.target.files.length > 0) {
      var file = e.target.files[0];
      var fd = new FormData();
      fd.append('avatar', file);
      var res = await axios.post('/upload', fd);
      setAvatarUrl(res.data[0]);
    }
  }

  async function register(e: React.FormEvent<HTMLFormElement>) {
    e.preventDefault();
    await axios.post('/account/register', {
      name: name.value,
      password: password.value,
      email: email.value,
      avatar: avatarUrl,
    });

    navigate('/login');
  }

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        height: '100%',
      }}
    >
      <h2 style={{ textAlign: 'center' }}>Register</h2>

      <form
        onSubmit={register}
        style={{
          display: 'flex',
          flexDirection: 'column',
          flex: 1,
          justifyContent: 'space-between',
        }}
      >
        <div style={{ display: 'flex' }}>
          <span style={{ flex: 1 }}>Name:</span>{' '}
          <Input size="large" type="text" required {...name} style={{ flex: 2 }} />
        </div>
        <div style={{ display: 'flex' }}>
          <span style={{ flex: 1 }}>Password:</span>{' '}
          <Input size="large" type="password" required {...password} style={{ flex: 2 }} />
        </div>
        <div style={{ display: 'flex' }}>
          <span style={{ flex: 1 }}>Email:</span>{' '}
          <Input size="large" type="email" required {...email} style={{ flex: 2 }} />
        </div>
        <div style={{ display: 'flex', flex: '3' }}>
          <span style={{ flex: 1 }}>Avatar:</span>{' '}
          <Input type="file" accept="image/*" onChange={handleFileSelect} style={{ flex: 2 }} />
        </div>
        <div style={{ display: 'flex', flex: '3', justifyContent: 'center', alignItems: 'center' }}>
          <Button type="primary" size="large" style={{ width: '200px', height: '100px' }}>
            注册
          </Button>
        </div>
      </form>
    </div>
  );
}
