import { Routes, Route, Navigate } from 'react-router-dom';
import SelectCreation from './SelectCreation';
import Home from './Home';
import CreateVote from './CreateVote';
import Login from './Login';
import ViewVote from './ViewVote';
import Me from './Me';
import Register from './Register';
export default function Vote() {
  return (
    <Routes>
      <Route path="" element={<Navigate to="home" />} />
      {/* 主页 */}
      <Route path="/home" element={<Home />}>
        {/* <Home />里面的<Outlet />直接是有index属性的element*/}
        <Route index element={<Navigate to="create" />}></Route>
        <Route path="create" element={<SelectCreation />}></Route>
        <Route path="me" element={<Me />}></Route>
      </Route>

      {/* 创建页面 */}
      <Route path="/create" element={<CreateVote />}></Route>
      <Route path="/vote/:id" element={<ViewVote />}></Route>
      <Route path="/login" element={<Login />}></Route>
      <Route path="/register" element={<Register />}></Route>
    </Routes>
  );
}
