import { Button, Input } from 'antd';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { useInput } from './hooks';

export default function Login() {
  const name = useInput('');
  const password = useInput('');
  const navigate = useNavigate();
  async function login() {
    // axios.post(路径, 请求体, config)
    // axios(路径, config)
    const res = await axios('/account/login', {
      method: 'post',
      data: {
        name: name.value,
        password: password.value,
      },
    });
    navigate('/');
  }
  return (
    <div
      style={{
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <div>
        <div style={{ display: 'flex', marginBottom: '30px' }}>
          <span style={{ flex: 0.6 }}>Username</span> <Input style={{ flex: 1 }} type="text" size="large" {...name} />
        </div>
        <div style={{ display: 'flex', marginBottom: '30px' }}>
          <span style={{ flex: 0.6 }}>Password</span>{' '}
          <Input style={{ flex: 1 }} type="text" size="large" {...password} />
        </div>
        <div style={{ display: 'flex', justifyContent: 'space-evenly', }}>
          <Button size="large" onClick={login} style={{ }}>
            登陆
          </Button>
          <Button type="primary" size="large" style={{ }}>
            <Link to="/register">注册</Link>
          </Button>
        </div>
      </div>
    </div>
  );
}
