import { useEffect } from 'react';
import { Link, useNavigate, useSearchParams } from 'react-router-dom';
import { useImmer } from 'use-immer';
import { useForceLogin, useInput } from './hooks';
import { useToggle } from 'ahooks';
import { Button, Checkbox, DatePicker, Form, Input, Switch } from 'antd';
import moment from 'moment';
import axios from 'axios';

import { DownloadOutlined } from '@ant-design/icons';

const map = {
  single: '单选',
  multiple: '多选',
} as const;

type VoteType = keyof typeof map;

export default function CreateVote() {
  useForceLogin();

  const [params, setParams] = useSearchParams();
  const [anonymous, { toggle }] = useToggle(false);
  const deadline = useInput('');
  const type = params.get('type') as VoteType;
  const title = useInput('');
  const desc = useInput('');
  const [options, updateOptions] = useImmer<string[]>(['', '']);
  const navigate = useNavigate();

  // 输错params,直接跳转到single页面
  useEffect(() => {
    if (type !== 'single' && type !== 'multiple') {
      setParams('type=single');
    }
  }, [type]);

  function addOption() {
    // options 是草稿draft
    updateOptions((options) => {
      options.push('');
    });
  }
  function deleteOption(idx: number) {
    updateOptions((options) => {
      options.splice(idx, 1);
    });
  }
  function editOption(e: React.ChangeEvent<HTMLInputElement>, idx: number) {
    updateOptions((options) => {
      options[idx] = e.target.value;
    });
  }
  async function createVote() {
    const voteInfo = {
      title: title.value,
      desc: desc.value,
      deadline: deadline.value,
      anonymous: anonymous,
      multiple: type === 'multiple',
      options: options,
    };
    const res = await axios.post('/vote', voteInfo);
    const voteId = res.data.result.voteId;

    // 跳转到查看用户投票的页面
    navigate('/vote/' + voteId);
  }
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
      }}
    >
      <h2
        style={{
          fontSize: '44px',
          color: 'whitesmoke',
          textAlign: 'center',
          fontWeight: '800',
        }}
      >
        创建{map[type]}投票
      </h2>
      {/* 投票主体 */}
      <Form>
        <Form.Item name={'title'}>
          <Input size="large" placeholder="投票标题" {...title} />
        </Form.Item>
      </Form>
      {/* === */}
      <Form style={{ flex: 1, overflow: 'auto' }}>
        <Form.Item name={'desc'}>
          <Input placeholder="补充描述(选填)" {...desc} size="large" />
        </Form.Item>
        <div>
          {options.map((option, idx) => {
            return (
              <Form.Item key={idx}>
                <div style={{ display: 'flex' }}>
                  <Button type="primary" onClick={() => deleteOption(idx)} size="large">
                    -
                  </Button>
                  <Input placeholder="选项" value={option} onChange={(e) => editOption(e, idx)} size="large" />
                </div>
              </Form.Item>
            );
          })}
        </div>

        <Form.Item>
          <Button onClick={addOption}>添加选项</Button>
        </Form.Item>
        <Form.Item label="截止日期" name={'deadline'} style={{ fontWeight: '800', color: 'white' }}>
          <DatePicker
            size="large"
            format="YYYY-MM-DDTHH:mm"
            showTime
            value={moment(deadline.value)}
            // m 是moment类型的对象
            onChange={(m) => deadline.onChange(m?.format('YYYY-MM-DDTHH:mm') ?? '')}
          ></DatePicker>
        </Form.Item>
        <Form.Item label="匿名投票" name="anonymous" style={{ fontWeight: '800', color: 'white' }}>
          <Switch checked={anonymous} onClick={toggle} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" onClick={createVote} size="large" style={{ marginRight: '100px' }}>
            投票提交
          </Button>
          <Button type="ghost" size="large">
            <Link to="/home" style={{ color: 'white', fontWeight: '900' }}>
              返回
            </Link>
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
